#job-manager-card {
	height: 80vh;
	min-width: 80vw;
}

#job-manager-card>.card-content {
	height: calc(100% - 40px);
	display: flex;
	flex-direction: column;
}

.job-info {
}

.tabs-card {
	height: 100%;
	padding: 0px;
	overflow: hidden;
	border-radius: 10px;
	border: 1px solid #ccc;
	background-color: #fafafa;
}

.tabs-card>.menu-bar {
	filter: unset;
	border-bottom: 1px solid #ccc !important;
	height: 40px;
	display: flex;
	width: 100%;
}
.tabs-card>.menu-bar>.menu-button {
	border-radius: 0px;
	height: 40px;
	line-height: 40px !important;
	border-bottom: 1px solid #ccc !important;
}
.tabs-card>.menu-bar>:last-child {
	border-right: 0px;
}
/*Jonas*/
.tabs-card>.menu-bar>:nth-child(1) {
	background-color: #4172bb;
}
.tabs-card>.menu-bar>:nth-child(1):hover,
.tabs-card>.menu-bar>:nth-child(1).selected {
	background-color: #2a518b;
}
/*Manufacturer*/
.tabs-card>.menu-bar>:nth-child(2) {
	background-color: #bb417a;
}
.tabs-card>.menu-bar>:nth-child(2):hover,
.tabs-card>.menu-bar>:nth-child(2).selected {
	background-color: #8b325c;
}
/*Ameren*/
.tabs-card>.menu-bar>:nth-child(3) {
	background-color: #309255;
}
.tabs-card>.menu-bar>:nth-child(3):hover,
.tabs-card>.menu-bar>:nth-child(3).selected {
	background-color: #1f643a;
}
/*Financing*/
.tabs-card>.menu-bar>:nth-child(4) {
	background-color: #923030;
}
.tabs-card>.menu-bar>:nth-child(4):hover,
.tabs-card>.menu-bar>:nth-child(4).selected {
	background-color: #742626;
}
/*Permit*/
.tabs-card>.menu-bar>:nth-child(5) {
	background-color: #653ead;
}
.tabs-card>.menu-bar>:nth-child(5):hover,
.tabs-card>.menu-bar>:nth-child(5).selected {
	background-color: #45277e;
}
/*PV Maintenance*/
.tabs-card>.menu-bar>:nth-child(6) {
	background-color: #ce9c30;
}
.tabs-card>.menu-bar>:nth-child(6):hover,
.tabs-card>.menu-bar>:nth-child(6).selected {
	background-color: #9e7a2b;
}
/*.tabs-card>.menu-bar>.menu-button.selected {
	background-color: #ccc;
	font-weight: 700;
}*/

.job-info-consultant {
	font-size: larger;
	font-weight: 550;
	color: #313131;
}
.job-info-progress {
	display: flex;
	gap: 10px;
}
.job-info-progress {
	font-size: large;
}
.job-info-progress>.progress {
	font-style: italic;
	font-weight: 550;
}
.job-info-perccomplete {
	font-style: italic;
	font-weight: 550;
	font-size: x-large;
}

.job-manager-form-cont {
	display: grid;
	flex-direction: column;
	width: 100%;
	height: calc(100% - 40px);
	grid-template-columns: 50% 50%;
	row-gap: 10px;
	gap: 10px;
	overflow-y: auto;
	overflow-x: hidden;
}
.job-manager-form-items-cont>.form-info-item {
	padding: 10px;
	padding-left: 20px;
	padding-right: 20px;
	display: grid;
	grid-template-columns: 40% 60%;
	align-items: center;
	justify-content: center;
}
.job-manager-form-items-cont>.form-info-item>:nth-child(2) {
	width: 100%;
}
.job-manager-form-items-cont>.form-info-item>input {
	padding: 10px;
}
.job-manager-form-items-cont>.form-info-item>.form-info-title {
	font-size: larger;
	font-weight: 550;
	color: #212121;
	font-style: italic;
}

.form-item-header {
	grid-template-columns: 100% 0% !important;
}

.form-item-header>.form-info-title {
	font-size: xx-large !important;
	color: #000 !important;
	font-weight: 900;
	width: 100%;
	font-style: normal !important;
}
.job-information {
	display: grid;
	grid-template-columns: 50% 50%;
}
#job-manager-controls {
	padding-top: 10px;
}

.job-info-base {
	width: 100%;
}

.job-info-cust {
	display: grid;
	grid-template-columns: 50% 50%;
	gap: 10px;
	border: 1px solid #ccc;
	padding: 10px;
	background-color: #f0f0f0;
	border-radius: 10px;
	font-size: large;
}
.job-info-cust>.form-info-item {
	display: grid;
	grid-template-columns: 29% 70%;
	gap: 1%;
}
.job-info-cust>.form-info-item>.form-info-title {
	font-weight: 600;
}

.job-info-base>.form-info-item {
	align-items: center;
	justify-content: left;
	gap: 10px;
}
.job-info-base>.form-info-item>.form-info-title {
	font-size: larger;
	font-weight: 600;
}

/*Close dialogue*/
.close-job-text {
	font-size: x-large;
	font-weight: 600;
	padding: 10px;
}

#close-job-button {
	background-color: #1f643a;
	font-size: 25px;
	font-weight: 600;
}

#close-job-button:hover {
	background-color: #19502e;
}

#job-manager-close-controls>.action-button {
	height: 50px;
	line-height: 50px;
	font-size: 25px;
}

#go-back-job-man-button {
	font-weight: 300;
	font-size: 20px !important;
}

.incomplete-fields {
	font-size: 35px;
}

.inc-fields-field {
	font-size: 30px;
	font-weight: 500;
	font-style: italic;
	padding: 10px;
}