#pv-maintenance-table-card {
	min-width: calc(100% - 10px);
	height: calc(100% - 50px);
	overflow: hidden;
}
#pv-maintenance-table-card>.card-content {
	padding: 0px;
}
#pv-maintenance-table-card>.card-content>.tablerow.header {
	background-color: #062a49;
	margin: 0px;
	padding: 0px;
}
#pv-maintenance-table-card>.card-content>.tablerow.header>div {
	padding: 0px;
	margin: 0px;
	background-color: transparent;
	color:#fff;
	font-size: x-large;
	line-height: 40px;
}